.text-ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cursor-pointer {
  cursor: pointer;
}

.overflow-hidden {
  overflow: hidden;
}

.zIndex {
  z-index: 40;
}

.navlink {
  padding: 5px 0px;
  text-decoration: none;
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
  margin: auto 20px;
  color: var(--black);
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, auto));
  width: -webkit-fill-available;
  justify-content: center;
}

.image-cnt {
  position: relative;
  height: 400;
  width: 300;
  border-radius: 10px;
  background: "#f2f2f2";
  margin: 10px 10px;
  padding: 10px 10px;
  box-shadow: 0 2px 5px 1px rgba(64, 60, 67, 0.1607843137);
  background-size: cover;
  transition: all 0.3s linear;
}

.image-cnt:hover {
  transform: scale(1.05) !important;
  box-shadow: 0 2px 5px 4px rgba(64, 60, 67, 0.1607843137);
}

.productImg {
  -o-object-fit: contain;
     object-fit: contain;
  width: 100%;
}

@media (max-width: 500px) {
  .productImg {
    max-width: 100%;
    height: 160px;
    -o-object-fit: cover;
       object-fit: cover;
  }
}
